* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  }

.loginblock{
  position: absolute;
  top: 10%;
  left: 30%;
  width: 270px;
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid grey;
  padding: 5px;
  border-radius: 10px;
}

.loginblock input {
  padding: 5px;
  width: 190px;
  border: 1px solid rgb(146, 156, 131);
  border-radius: 15px;
  height: 25px;
  margin: 5px;
}

.loginblock2{
  position: absolute;
  top: 45%;
  left: 30%;
  width: 270px;
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
